.header {
  padding-top: 24px;
}

.header-nav {
  display: none;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-modal-item {
  color: var(--dark-main-white-text-color);
  transition: var(--animation);
  font-size: 12px;
  font-weight: 400;
}

.header-modal-item:-webkit-any(:hover, :focus) {
  color: var(--light-main-black-text-color);
  font-weight: 500;
}

.header-modal-item:is(:hover, :focus) {
  color: var(--light-main-black-text-color);
  font-weight: 500;
}

.header-tastytreats {
  color: var(--light-main-black-text-color);
  letter-spacing: -.64px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 600;
}

.header-tasty {
  color: var(--light-main-accent-color);
}

.header-buttons {
  align-items: center;
  gap: 14px;
  display: flex;
}

.header-btn-shopping-cart {
  height: 24px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

.header-btn-burger-menu {
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

.svg-shopping-cart:is(:hover, :focus) path, .svg-burger-menu:hover path {
  stroke: var(--light-main-accent-color);
  transition: var(--animation);
}

.header-btn-burger-menu:focus .svg-burger-menu path, .header-btn-shopping-cart:focus-visible .svg-shopping-cart path {
  stroke: var(--light-main-accent-color);
}

.switch {
  width: 46px;
  height: 20px;
  display: none;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background-color: #cecdcd;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: #f8f8f8;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

input:checked + .slider {
  background-color: #9bb537;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.display-none {
  display: none;
}

@keyframes ani {
  0% {
    transform: translateX(150%);
  }

  100% {
    transform: translateY(0);
  }
}

.header-back {
  z-index: 98;
  height: 100%;
  width: 100%;
  -webkit-backdrop-filter: blur(7px) brightness(.86) grayscale(28%) opacity(92%);
  backdrop-filter: blur(7px) brightness(.86) grayscale(28%) opacity(92%);
  transition: var(--animation);
  position: fixed;
  top: 0;
  left: 0;
}

.header-modal {
  z-index: 99;
  width: 50%;
  height: 100%;
  background-color: var(--light-main-accent-color);
  animation: ani var(--animation) forwards;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  overflow: auto;
  transform: translateX(-150%);
}

.header-modal::-webkit-backdrop {
  -webkit-backdrop-filter: blur(7px) brightness(.86) grayscale(28%) opacity(92%);
  backdrop-filter: blur(7px) brightness(.86) grayscale(28%) opacity(92%);
  transition: var(--animation);
}

.header-modal::backdrop {
  -webkit-backdrop-filter: blur(7px) brightness(.86) grayscale(28%) opacity(92%);
  backdrop-filter: blur(7px) brightness(.86) grayscale(28%) opacity(92%);
  transition: var(--animation);
}

.header-btn-close {
  height: 32px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  position: absolute;
  top: 20px;
  right: 20px;
}

.svg-btn-close {
  stroke: #f8f8f8;
}

.svg-btn-close:hover, .svg-btn-close:focus {
  stroke: var(--light-main-black-text-color);
}

.header-modal-list {
  flex-direction: column;
  align-items: center;
  gap: 16px;
  margin-top: 200px;
  display: flex;
}

.switch-modal {
  width: 46px;
  height: 20px;
  z-index: 999;
  display: inline-block;
  position: absolute;
  bottom: 20px;
  left: 20px;
}

@media only screen and (min-width: 768px) {
  .header {
    padding-top: 28px;
  }

  .header-tastytreats {
    letter-spacing: -.72px;
    font-size: 18px;
    line-height: 1.1;
  }

  .slider {
    box-shadow: 10px 6px 8px #cecdcd;
  }

  input:checked + .slider:before {
    transform: translateX(26px);
    box-shadow: 6px 6px 14px #f3f3f3;
  }

  .switch {
    display: inline-block;
  }

  .header-nav {
    display: flex;
  }

  .header-btn-burger-menu {
    display: none;
  }

  .header-refs {
    gap: 16px;
    display: flex;
  }

  .header-ref {
    color: var(--light-main-black-text-color);
    transition: var(--animation);
    font-size: 12px;
    font-weight: 400;
    line-height: 1.3;
  }

  .header-ref:-webkit-any(:hover, :focus) {
    color: var(--light-main-accent-color);
  }

  .header-ref:is(:hover, :focus) {
    color: var(--light-main-accent-color);
  }

  .header-active {
    color: var(--light-main-accent-color);
